import {  Component, ContentChildren, Directive, ElementRef, OnInit, QueryList} from '@angular/core';
import { CanDisableCtor, mixinDisabled } from 'tds-ui/shared/common';

class TDSScrollIntoViewBase {
  constructor(public _elementRef: ElementRef) { }
}
const _TDSScrollIntoViewBase: CanDisableCtor &
  typeof TDSScrollIntoViewBase = mixinDisabled(TDSScrollIntoViewBase);

@Directive({
  selector: `[tds-scroll-into-view]`,
  host: {
    '[disabled]': 'disabled ||  null',
  },
  inputs: ['disabled', 'scrollID']
})
export class TDSScrollIntoView extends _TDSScrollIntoViewBase {

  private _scrollID: any;
  get scrollID() {
    return this._scrollID;
  }
  set scrollID(val: any) {
    this._scrollID = val;
  }
  constructor(elementRef: ElementRef) {
    super(elementRef)
  }

  public getScrollID() {
    return this._scrollID;
  }
  public getElement(): ElementRef {
    return this._elementRef;
  }
}

@Component({
  selector: 'tds-scroll-into-view',
  template: `
  <ng-content></ng-content>
  `,
  host: {
    'class': "block relative w-full overflow-auto"
  }
})
export class TDSScrollIntoViewComponent extends _TDSScrollIntoViewBase implements OnInit {
  @ContentChildren(TDSScrollIntoView) lstChildren!: QueryList<TDSScrollIntoView>;
  constructor(elementRef: ElementRef) {
    super(elementRef)
  }

  ngOnInit(): void {
  }
  public scrollIntoView(dataID: any) {
    console.log(dataID);
    const children = this.lstChildren.find(e => {
      return e.getScrollID() === dataID;
    });
    if (children) {
      children.getElement().nativeElement.scrollIntoView({ behavior: "smooth", block: "end" });
    }

  }

}
